<template>
  <div class="index-warp">
    <div class="col-wrap" v-if="detectionStatus">
      <div class="t-10 t-fixed"></div>
      <el-row :gutter="20" class="col-title">
        <el-col :span="11">
          <div
            v-if="finaTime-theTime <= 0"
            class="bold"
          >{{detectionData.name}}在规定日期（{{detectionData.finalDate}}）内未补交主营类目保证金，系统将暂停您的使用，敬请谅解！</div>
          <div
            v-else
            class="bold"
          >应平台对商城主营分类保证金进行调整，所有店铺需要在{{detectionData.difference}}天（{{detectionData.finalDate}}）内进行补齐，到期未缴纳，系统将暂停您的使用，敬请谅解！</div>
        </el-col>
        <el-col :span="4">
          <span @click="immediate" class="immediate" :style="'background-color:'+color">补交保证金</span>
        </el-col>
        <el-col :span="9"></el-col>
      </el-row>
    </div>
    <el-row :gutter="20">
      <el-col :span="12">
        <div class="col-warp t-height">
          <h3>待办事务</h3>
          <ul class="index-counts">
            <li class="index-count">
              <count-to :endVal="data.waitOrders" class="index-val"></count-to>
              <p>待发货订单</p>
            </li>
            <li class="index-count">
              <count-to :endVal="data.sendOrders" class="index-val"></count-to>
              <p>已发货订单</p>
            </li>
            <li class="index-count">
              <count-to :endVal="data.refunds" class="index-val"></count-to>
              <p>售后订单</p>
            </li>
            <li class="index-count">
              <count-to :endVal="data.products" class="index-val"></count-to>
              <p>违规下架商品</p>
            </li>
          </ul>
        </div>
        <div class="col-warp t-height">
          <h3>快捷入口</h3>
          <ul class="index-counts w20">
            <li class="index-count">
              <router-link to="/product/save">
                <img src="./../assets/images/index/w-1.png" alt>
                <p>商品发布</p>
              </router-link>
            </li>
            <li class="index-count">
              <router-link to="/orderManger/list">
                <img src="./../assets/images/index/w-2.png" alt>
                <p>订单列表</p>
              </router-link>
            </li>
            <li class="index-count">
              <router-link to="/delivery">
                <img src="./../assets/images/index/w-3.png" alt>
                <p>订单发货</p>
              </router-link>
            </li>
            <li class="index-count">
              <router-link to="/storeActivity/list">
                <img src="./../assets/images/index/w-4.png" alt>
                <p>活动发布</p>
              </router-link>
            </li>
            <li class="index-count">
              <router-link to="/admin/list">
                <img src="./../assets/images/index/w-5.png" alt>
                <p>新增员工</p>
              </router-link>
            </li>
          </ul>
        </div>
      </el-col>
      <el-col :span="12">
        <el-row :gutter="20">
          <el-col :span="12">
            <div class="circle-card">
              <div class="circle-card__body">
                <div class="circle-desc">
                  <p>订单数</p>
                  <count-to :endVal="introduceData.order.orders" class="val"></count-to>
                </div>
                <el-progress
                  type="circle"
                  :percentage="introduceData.order.ordersPrecent"
                  color="#f0b68f"
                  status="text"
                  :width="70"
                  :stroke-width="8"
                  class="circle-warp"
                >
                  {{introduceData.order.ordersPrecent}}%
                  <br>占昨日
                </el-progress>
              </div>
              <div class="circle-card__foot" style="background:#fceee4">
                <span class="fl">昨日全天</span>
                <count-to :endVal="introduceData.order.yesterdayOrders" class="val"></count-to>
              </div>
            </div>
          </el-col>
          <el-col :span="12">
            <div class="circle-card">
              <div class="circle-card__body">
                <div class="circle-desc">
                  <p>付款订单数</p>
                  <count-to :endVal="introduceData.payOrder.payorders" class="val"></count-to>
                </div>
                <el-progress
                  type="circle"
                  :percentage="introduceData.payOrder.payOrdersPrecent"
                  color="#f08f98"
                  status="text"
                  :width="70"
                  :stroke-width="8"
                  class="circle-warp"
                >
                  {{introduceData.payOrder.payOrdersPrecent}}%
                  <br>占昨日
                </el-progress>
              </div>
              <div class="circle-card__foot" style="background:#fce4e7">
                <span class="fl">昨日全天</span>
                <count-to :endVal="introduceData.payOrder.yesterdayPayorders" class="val"></count-to>
              </div>
            </div>
          </el-col>
          <el-col :span="12">
            <div class="circle-card">
              <div class="circle-card__body">
                <div class="circle-desc">
                  <p>售后订单数</p>
                  <count-to :endVal="introduceData.refund.refunds" class="val"></count-to>
                </div>
                <el-progress
                  type="circle"
                  :percentage="introduceData.refund.refundsPrecent"
                  color="#8f98f0"
                  status="text"
                  :width="70"
                  :stroke-width="8"
                  class="circle-warp"
                >
                  {{introduceData.refund.refundsPrecent}}%
                  <br>占昨日
                </el-progress>
              </div>
              <div class="circle-card__foot" style="background:#e4e7fc">
                <span class="fl">昨日全天</span>
                <count-to :endVal="introduceData.refund.yesterdayRefunds" class="val"></count-to>
              </div>
            </div>
          </el-col>
          <el-col :span="12">
            <div class="circle-card">
              <div class="circle-card__body">
                <div class="circle-desc">
                  <p>访问量</p>
                  <count-to :endVal="introduceData.visit.visits" class="val"></count-to>
                </div>
                <el-progress
                  type="circle"
                  :percentage="introduceData.visit.visitsPresent"
                  color="#8fb5f0"
                  status="text"
                  :width="70"
                  :stroke-width="8"
                  class="circle-warp"
                >{{introduceData.visit.visitsPresent}}%
                  <br>占昨日
                </el-progress>
              </div>
              <div class="circle-card__foot" style="background:#e4eefc">
                <span class="fl">昨日全天</span>
                <count-to :endVal="introduceData.visit.yesterdayVisits" class="val"></count-to>
              </div>
            </div>
          </el-col>
        </el-row>
      </el-col>
    </el-row>
    <index-chart :data="introduceData.option"></index-chart>
    <service></service>
  </div>
</template>

<script>
import countTo from 'vue-count-to'
import indexChart from './index/chart'
import service from './index/service'

import {
  storeSummarizeTransaction,
  storeSummarizeIntroduce,
  storeAccountInfoDetection
} from './../api/index/index.js'

export default {
  components: {
    countTo,
    indexChart,
    service
  },
  data() {
    return {
      status: false,
      data: {
        products: 0,
        refunds: 0,
        sendOrders: 0,
        waitOrders: 0
      },
      introduceData: {
        visit: {
          visits: 0,
          visitsPresent: 0,
          yesterdayVisits: 0
        },
        order: {
          ordersPrecent: 0,
          yesterdayOrders: 0,
          orders: 0
        },
        payOrder: {
          payOrdersPrecent: 0,
          yesterdayPayorders: 0,
          payorders: 0
        },
        refund: {
          yesterdayRefunds: 0,
          refundsPrecent: 0,
          refunds: 0
        },
        option: {}
      },
      detectionData: {},
      detectionStatus: false,
      theTime: new Date().getTime(),
      finaTime: null
    }
  },
  computed: {
    color() {
      return this.$store.state.system.color
    }
  },
  methods: {
    immediate() {
      this.$routerLink('/store/account/info', 'view', { active: 5 })
    },
    getData() {
      storeAccountInfoDetection().then(res => {
        if (res.code === 13023) {
          this.finaTime = new Date(res.data.finalDate).getTime()
          this.detectionStatus = true
          this.detectionData = res.data
        } else {
          this.detectionStatus = false
        }
      })
      storeSummarizeTransaction().then(res => {
        if (res.code === 200) {
          this.data = res.data
        }
      })
      storeSummarizeIntroduce().then(res => {
        if (res.code === 200) {
          this.status = true
          this.introduceData = res.data
        }
      })
    }
  },
  created() {
    this.getData()
  },
  mounted() {}
}
</script>
<style lang="scss" scoped>
.col-wrap {
  height: 60px;
  width: 100%;
  overflow: hidden;
}
.bold {
  font-weight: bold;
}
.t-10 {
  width: 100%;
  height: 10px;
  background-color: #f2f2f2;
}
.t-fixed {
  position: fixed;
  top: 94px;
  z-index: 12;
}
.immediate {
  display: inline-block;
  color: #fff;
  width: 70px;
  text-align: center;
  line-height: 36px;
  height: 36px;
  cursor: pointer;
}
.col-title {
  position: fixed;
  top: 104px;
  z-index: 12;
  width: 1440px;
  background-color: #fff7cd;
  margin: 0 auto !important;
  padding: 12px 10px;
}
.circle-desc {
  float: left;
  p {
    margin-top: 10px;
  }
  .val {
    display: inline-block;
    font-weight: bold;
    font-size: 18px;
    margin-top: 8px;
  }
}
.circle-warp {
  float: right;
  font-size: 14px;
}
.circle-card {
  height: 130px;
  background: #fff;
  margin-top: 20px;
  color: #666;
  &__body {
    overflow: hidden;
    padding: 20px;
    height: 95px;
    font-size: 14px;
  }
  &__foot {
    background: #e4eefc;
    padding: 5px 20px;
    font-size: 14px;
    overflow: hidden;
    height: 35px;
    .fl {
      line-height: 23px;
      position: relative;
      top: 2px;
    }
    .val {
      float: right;
      font-size: 16px;
      font-weight: bold;
    }
  }
}

.index-warp {
  padding: 0 0 20px 0;
  position: relative;
}
.t-height{
    height: 130px;
  }
.col-warp {
  background: #fff;
  padding: 15px 20px 20px 20px;
  margin-top: 20px;
  h3 {
    font-size: 14px;
    color: #333;
    font-weight: normal;
  }
}
.index-counts {
  overflow: hidden;
}
.index-count {
  float: left;
  width: 25%;
  text-align: center;
  margin-top: 15px;
  font-size: 14px;
  cursor: pointer;
  p {
    font-size: 14px;
    color: #686868;
  }
}
.w20 {
  .index-count {
    width: 20%;
    margin-top: 20px;
    img {
      width: 30px;
      height: 30px;
    }
  }
}
.index-val {
  color: #79a9f2;
  font-size: 26px;
  font-weight: 500;
}
</style>




