<template>
  <div class="col-warp service-warp">
    <el-tabs v-model="activeName">
      <el-tab-pane label="开通服务" name="first">
        <el-row :gutter="20">
          <el-col :span="6" v-for="(item) in data" :key="item.name" class="service-bg">
            <div class="service-card">
              <p class="service-name">{{item.name}}</p>
              <p class="service-time" v-if="!item.selfshop">
                <span v-if="item.status">{{item.endTime}}到期</span>
                <span v-show="item.day<=7" class="error">(剩余{{item.day}}天)</span>
                <a v-show="!item.status" class="t-color">开通</a>
              </p>
              <p class="service-time" v-if="item.selfshop">
                <span v-if="item.status">已开通</span>
                <a v-show="!item.status" class="t-color">开通</a>
              </p>
               <router-link v-if="!item.status" to="/marketingService/list"  class="t-color">去开通</router-link>
              <i class="s-bg s1"></i>
            </div>
          </el-col>
        </el-row>
      </el-tab-pane>
      <el-tab-pane label="参与活动" name="second">
            <remote-list></remote-list>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import { storeSummarizeService } from './../../api/index/index.js'
import remoteList from './remote'
export default {
  components: {
    remoteList
  },
  data() {
    return {
      activeName: 'first',
      data: []
    }
  },
  methods: {
    getData() {
      storeSummarizeService().then(res => {
        if (res.code === 200) {
          this.data = res.data
        }
      })
    }
  },
  created() {
    this.getData()
  }
}
</script>

<style lang="scss" scoped>
.service-card {
  border-radius: 3px;
  height: 90px;
  padding: 20px;

  position: relative;
}
.service-bg {
  margin-bottom: 20px;
  &:nth-child(3n) {
    .service-card {
      background: #fceee4;
    }
  }
  &:nth-child(3n + 2) {
    .service-card {
      background: #fce4e7;
    }
  }
  &:nth-child(3n + 1) {
    .service-card {
      background: #e4eefc;
    }
  }
}
.service-name {
  font-size: 18px;
}
.service-warp {
  height:auto;
}
.service-time {
  margin-top: 8px;
  font-size: 12px;
  color: #666;
  .error {
    margin-left: 10px;
    color: red;
  }
}
.s-bg {
  position: absolute;
  width: 60px;
  height: 60px;
  right: 20px;
  top: 10px;
  display: block;
  background: no-repeat;
}
.s1 {
  background: url("./../../assets/images/index/s-1.png");
}
.t-color{
  color: #40A3DC;
  position: relative;
  left: 10px;
}
</style>




