<template>
  <div class="col-warp chart-warp">
    <h3>今日付款订单数实时变化</h3>
    <div id="lineChart"></div>
  </div>
</template>

<script>
import echarts from 'echarts'
export default {
  props: {
    data: {
      type: Object,
      default: function() {
        return {}
      }
    }
  },
  data() {
    return {
      el: 'lineChart',
      chart: null,
      optionsData: {
        grid: {
          top: 10,
          left: 40,
          right: 0
        },
        tooltip: {
          trigger: 'axis'
        },
        xAxis: {
          type: 'category',
          splitLine: {
            show: false
          },
          data: ['00-00', '00-00', '00-00', '00-00', '00-00', '00-00', '00-00']
        },
        yAxis: {
          type: 'value',
          axisLine: {
            show: false
          },
          splitLine: {
            lineStyle: {
              type: 'dotted'
            }
          }
        },
        series: [
          {
            data: [23, 424, 24, 122, 23, 232, 223],
            type: 'line',
            smooth: true
          }
        ]
      }
    }
  },
  watch: {
    data: {
      handler(newVal, oldVal) {
        this.getData(this.data)
      },
      deep: true
    }
  },
  methods: {
    getData(data) {
      this.optionsData.xAxis.data = data.xAxis
      this.optionsData.series[0].data = data.yAxis
      this.chart.setOption(this.optionsData)
    }, // 只要复写这个函数就可以操作对应的图表数据,
    initChart() {
      // 初始化图表
      this.$nextTick(() => {
        this.chart = echarts.init(document.getElementById(this.el), 'walden')
      })
    }
  },
  mounted() {
    // 监听图表
    this.initChart()
    window.addEventListener('resize', () => {
      this.chart.resize()
    })
  }
}
</script>

<style>
#lineChart {
  margin-top: 20px;
  height: 250px;
  width: 100%;
}
.chart-warp {
  height: 300px;
}
</style>



