<template>
  <el-row :gutter="20" class="remote-body">
    <el-col :span="12">
      <div class="remote-header">
        <span class="fl">店铺发布活动</span>
        <router-link class="fr" to="/storeActivity/list">
          <span class="el-icon-more"></span>
        </router-link>
      </div>
      <el-table :data="listData.store" stripe>
        <el-table-column label="名称" prop="name"></el-table-column>
        <el-table-column label="有效期" prop="time"></el-table-column>
        <el-table-column label="状态" prop="status"></el-table-column>
      </el-table>
    </el-col>
    <el-col :span="12">
      <div class="remote-header">
        <span class="fl">平台活动</span>
        <router-link class="fr" to="/platformActivity/list">
          <span class="el-icon-more fr"></span>
        </router-link>
      </div>
      <el-table :data="listData.platform" stripe>
        <el-table-column label="名称" prop="name"></el-table-column>
        <el-table-column label="有效期" prop="time"></el-table-column>
        <el-table-column label="状态" prop="status">
          <template slot-scope="scope">
            <span 
            @click="signUp(scope.row.id,scope.row.status==='活动进行中'?2:1)" 
            :style="[{color:scope.row.status==='活动进行中'?'red':''},
            {cursor:scope.row.status==='活动进行中'?'':'pointer'}]">{{scope.row.status}}</span>
          </template>
        </el-table-column>
      </el-table>
    </el-col>
  </el-row>
</template>

<script>
import { storeSummarizePromote } from './../../api/index/index.js'
export default {
  data() {
    return {
      listData: []
    }
  },
  methods: {
    getData() {
      storeSummarizePromote().then(res => {
        if (res.code === 200) {
          this.listData = res.data
        }
      })
    },
    signUp(id, type) {
      if (type === 1) {
        this.$routerLink('/platformActivity/edit', 'view', { id: id })
      }
    }
  },
  created() {
    this.getData()
  }
}
</script>

 <style lang="scss" scoped>
.remote-body {
  margin-bottom: 20px;
}
.remote-header {
  font-size: 14px;
  color: #303133;
  height: 35px;
  overflow: hidden;
  padding: 0 20px 0 0;
  span {
    line-height: 35px;
  }
  .el-icon-more {
    cursor: pointer;
  }
}
</style>




